.token-info {
	padding: 5px;
	position: absolute;
	z-index: 9999;
	cursor: default;
	-webkit-animation: .5s fadeIn;
	        animation: .5s fadeIn;
}

.token-info__content {
	padding: 10px;
	border-radius: 5px;
	background: #000;
	color: #fff;
    width: 450px;
    height: 150px;
    overflow: auto;
    opacity: 0.95;
}

.token-info:after {
	content: '';
	width: 0;
	height: 0;
	margin: auto;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	border: 5px solid transparent;
}

.token-info--top:after {
	top: auto;
	border-bottom: none;
	border-top-color: #000;
}

.token-info--left:after {
	left: auto;
	border-right: none;
	border-left-color: #000;
}

.token-info--right:after {
	right: auto;
	border-left: none;
	border-right-color: #000;
}

.token-info--bottom:after {
	bottom: auto;
	border-top: none;
	border-bottom-color: #000;
}

@-webkit-keyframes fadeIn {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}
}



